    <template>
    <div>
        <h2 class="mb20"></h2>
        <el-form ref="form" :rules="rules" :model="form" label-width="100px" class="demo-ruleForm">
            <el-form-item label=":" prop="title">
                <el-input v-model="form.title"></el-input>
            </el-form-item>
            <el-form-item label=":" >
                <el-upload
                action="https://jsonplaceholder.typicode.com/posts/"
                list-type="picture-card"
                :on-preview="handlePictureCardPreview"
                :on-remove="handleRemove">
                <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
                <span class="gray"></span>
            </el-form-item>
            <el-form-item label=":" >
                <el-upload
                action="https://jsonplaceholder.typicode.com/posts/"
                list-type="picture-card"
                :on-preview="handlePictureCardPreview2"
                :on-remove="handleRemove2">
                <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible2">
                <img width="100%" :src="dialogImageUrl2" alt="">
                </el-dialog>
                <span class="gray"></span>
            </el-form-item>
            <el-form-item label=":" prop="describe">
                <el-input
                type="textarea"
                :rows="2"
                placeholder=""
                v-model="form.describe">
                </el-input>
            </el-form-item>
            <el-form-item label=":" prop="newsContent">
                <el-input v-model="form.title2"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" class="btn-primary"></el-button>
                <el-button type="primary" class="btn-default"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
// const UE = require('ueditor')
export default {
    data(){
        return{
            form: {
                title: '',
                title2: '',
                describe: '',
                default: '',
                source: {
                    keyword: '',
                    default: ''
                },
                priority: 0
            },
            rules: {
                title: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 50, message: ' 3  50 ', trigger: 'blur' }
                ],
                describe: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
                ],
                priority: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 1, max: 2, message: ' 1  99', trigger: 'blur' }
                ]
            },
            dialogImageUrl: '',
            dialogImageUrl2: '',
            dialogVisible: false,
            dialogVisible2: false,
            value3: 0,
            dynamicTags: [],
            inputVisible: false,
            inputValue: ''
        }   
    },
    methods: {
        handleClose(tag) {
            this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
        },
        showInput() {
            this.inputVisible = true;
            this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        handleInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
            this.dynamicTags.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = '';
        },
        handleRemove(file, fileList) {
            
        },
        handleRemove2(file, fileList) {
            
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        handlePictureCardPreview2(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        }
    },
    mounted(){
        
    }
}
</script>
<style lang="less" scoped>
.el-form-item{
    width: 100%;
}
 .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .button-new-tag{
      margin-left: 0;
  }
</style>



