<template>
    <div>
        <div class="clearfix user">
            <div class="fl">
                <el-form :model="query" ref="query">
                    <el-form-item>
                        <el-select v-model="query.default" placeholder="" size="small">
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                            <el-option label="ID" value="3"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-input placeholder="" v-model="query.keyword" size="small"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="queryForm" size="small"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="fr">
                <el-form>
                    <el-form-item>
                        <el-button type="primary" size="small" @click="createAccount = true"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <router-link :to="{path: 'home/roleList'}"><el-button type="primary" size="small" ></el-button></router-link>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="small"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="table-list">
            <el-table
                :data="tableListData"
                border
                style="width: 100%"
                >
                <el-table-column
                prop="_id"
                label="ID()"
                align="center">
                </el-table-column>
                <el-table-column
                prop="phone"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="username"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="userRole"
                label=""
                align="center">
                    <template slot-scope="scope">
                        <span v-if="scope.row.userRole == '1'">VIP</span>
                        <span v-if="scope.row.userRole == '2'"></span>
                        <span v-if="scope.row.userRole == '3'"></span>
                    </template>
                </el-table-column>
                <el-table-column
                 prop="state"
                label=""
                align="center">
                    <template slot-scope="scope">
                        <span v-if="scope.row.state === 1"></span>
                        <span v-if="scope.row.state === 0"></span>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                align="center">
                    <template slot-scope="scope">
                        {{compileDate(scope.row.recentLoginTime)}}
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                align="center">
                    <template slot-scope="scope">
                        <a href="javascript:" class="blue" @click="editorOpen(scope.row)"></a>
                        <a href="javascript:" class="red ml20"></a>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="page">
            <el-pagination
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            background
            layout="prev, pager, next,total"
            :total="total">
            </el-pagination>
        </div>
        <!--  --> 
        <el-dialog title="" :visible.sync="createAccount" width="25%" center :before-close="userClose">
            <el-form :model="createForm" size="small" :rules="rules" hide-required-asterisk status-icon ref="createForm" label-width="80px" class="demo-ruleForm create-form">
                <el-form-item label="" prop="checkPhone">
                    <el-input type="text" v-model="createForm.checkPhone" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="pass">
                    <el-input type="password" v-model="createForm.pass" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="checkPass">
                    <el-input type="password" v-model="createForm.checkPass" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="username">
                    <el-input type="text" v-model="createForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="createUserRole">
                    <el-select class="w100b" v-model="createForm.createUserRole" placeholder="" size="small">
                        <el-option label="1 - VIP" value="1"></el-option>
                        <el-option label="2 - " value="2"></el-option>
                        <el-option label="3 - " value="3"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button :plain="true" type="primary" size="small" @click="submitForm('createForm')"></el-button>
            </div>
        </el-dialog>
        <!--  --> 
        <el-dialog title="" :visible.sync="editorUser" width="25%" center :before-close="editorUserClose">
            <el-form :model="editorForm" size="small" hide-required-asterisk status-icon label-width="80px" class="demo-ruleForm create-form">
                <el-form-item label="">
                    <el-input type="text" v-model="editorForm.phone" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="password" v-model="editorForm.password" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="editorForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select class="w100b" v-model="editorForm.editorRole" size="small">
                        <el-option
                        v-for="item in editorForm.editorUserRole"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" size="small" @click="editor"></el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {dateFor} from '../../api/utils'
export default {
    name:'adminUser',
    data(){
        //
        var checkPhone = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            } else {
                const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
                console.log(reg.test(value));
            if (reg.test(value)) {console.log(value)
                this.$axios.post('/checkUser',{type:1,value:value}).then(function(res){
                    console.log(res)
                    if(res.status){
                        return callback(new Error(''));
                    } else{
                        callback();
                    }
                })
            } else {
                    return callback(new Error(''));
                }
            }
        };
        //
        var pass = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            } else {
            const reg = /^[a-zA-Z]\w{5,17}$/
            console.log(reg.test(value));
            if (reg.test(value)) {
                callback();
            } else {
                return callback(new Error('6~18,'));
            }
            }
        };
        //
        let checkPass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''))
            } else if (value !== this.createForm.pass) {
                callback(new Error('!'))
            } else {
                callback()
            }
        };
        //
        var username = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            } else {
                const reg = /^.{2,20}$/
                console.log(reg.test(value));
            if (reg.test(value)) {
                this.$axios.post('/checkUser',{type:2,value:value}).then(function(res){
                    console.log(res)
                    if(res.status){
                        return callback(new Error(''));
                    } else{
                        callback();
                    }
                })
            } else {
                    return callback(new Error(' 2  5 '));
                }
            }
        };
        return{
            //
            createAccount: false,
            editorUser: false,
            //
            total: 0,
            pageSize: 13,
            pageIndex: 1,
            //
            editorForm: {
                userId: '',
                phone: '',
                password: '',
                username: '',
                editorRole: '',
                editorUserRole:[{
                    value: '1',
                    label: 'VIP'
                },{
                    value: '2',
                    label: ''
                },{
                    value: '3',
                    label: ''
                }]
            },
            //
            createForm: {
                checkPhone: '',
                pass: '',
                createUserRole: '',
                username:'',
            },
            tableListData:[],
            //
            query: {
                keyword: '',
                default: '1'
            },
            //
            rules: {
                checkPhone: [
                    {validator: checkPhone, trigger: 'blur'}
                ],
                pass: [
                    {validator: pass, trigger: 'blur'}
                ],
                checkPass: [
                    {validator: checkPass, trigger: 'blur'}
                ],
                username: [
                    {validator: username, trigger: 'blur'}
                ],
                createUserRole: [
                    { required: true, message: '', trigger: 'change' }
                ],
            }
        }
    },
    methods: {
        //
        editorOpen(row){
            let that = this;
            that.editorUser = true;
            console.log(row)
            that.editorForm.userId = row.phone;
            that.editorForm.phone = row.phone;
            that.editorForm.username = row.username;
            that.editorForm.editorRole = row._id;
        },
        editor(){

        },
        //
        queryForm(){
            let that = this;
            if(that.query.keyword.length >0){
                that.$axios.post('/queryUser',{
                    type: that.query.default,
                    keyword: that.query.keyword
                }).then((res)=>{
                    that.tableListData = res.data.data;
                })
            } else{
                that.tableList(that.pageSize,that.pageIndex);
            }
        },
        //
        handleCurrentChange(val){console.log(666)
            var that = this;
            that.pageIndex = val;
            that.tableList(that.pageSize,that.pageIndex);
        },
        //
        tableList(pageSize,pageIndex){
            let that = this;
            that.$axios.post('/adminUser',{
                pageSize: pageSize,
                pageIndex: pageIndex,
                count: 1
            }).then((res)=>{
                that.tableListData = res.data.items;
                that.total = res.data.itemsTotle;
            })
        },
        userClose(done){
            done();
        },
        editorUserClose(done){
            done();
        },
        /*  */
        compileDate(time){
            return dateFor.dateFormat(time)
        },
        submitForm(formName){
            let that = this;
            let obj = JSON.stringify(this.createForm);
            that.$refs[formName].validate((valid) => {
                if (valid) {
                    that.$axios.post('/createUser',{formData:obj}).then((res)=>{
                        //
                        that.$message({
                            title: '',
                            message: '',
                            type: 'success',
                            center: true    
                        });
                        //
                        that.createAccount = false;
                        //
                        that.$refs.createForm.resetFields();
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    },
    mounted(){
        this.tableList(this.pageSize,this.pageIndex)
    }
}
</script>
<style lang="less" scoped>
.el-pagination{
    text-align: center;
}
.el-dialog__header{
    text-align: left;
}
.el-form-item--small.el-form-item{
    margin-bottom: 30px;
}
.el-dialog--center .el-dialog__body{
    padding: 5px 25px;
}
.create-form .el-form-item{
    width: 100%;
}
.el-form-item__content{
    width: 100%;
}
.w94{
    width: 94%;
}
.w100b{
    width: 100%;
}
.el-select .el-tag{
    border-color: rgba(32,160,255,.2);
    color: #20a0ff;
    height: 24px;
    line-height: 24px;
}
</style>


